@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@500&display=swap);
/* imported google font  */

html {
  box-sizing: border-box;
  display: grid;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(https://images.pexels.com/photos/414660/pexels-photo-414660.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260);
  background-size: cover;
}

code {
  font-family: 'Open Sans', sans-serif;
}


/* imported Font-Family */

.wrapper {
  display: grid;

  grid-template-areas: 
    ". . about"
    "header . projects"
    ". . bs"
    "footer . ."
  ;
}

.header {
  display:grid;
  grid-area: header;
  grid-column-gap: 10px;
  grid-template-areas: 
    "name-header picture"
    "email phone"
    "linkedin resume"
  ;

  width: 650px;
  height: 400px;

  font-size: 25px;
  color: rgb(70, 70, 70);

  background-color: white;
  background-image: url(/portfolio/static/media/puzzle_piece.5d851c19.jpg);
  background-position: bottom;
  background-size: cover;

  border: 1.5px solid rgb(208, 200, 204);
  border-radius: 10px;

  margin-top: -105px;
  margin-left: 28px;
  padding: 15px;
}

.pic-irene {
  grid-area: picture;

  height: 150px;
  border-radius: 100%;
  margin-left: 15px;
  margin-top: 15px;

  box-shadow: 0 0 100px rgba(208, 200, 204, 1);
}

.name-title {
  grid-area: name-header;

  text-shadow: -2px -1px 1px rgb(208, 200, 204);
  font-family: 'Inconsolata', monospace;

  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
}

.git {
  position: relative;
  top: 3px;

  font-size: 20px;
}

.email, .phone, .resume, .linkedin {
  text-shadow: 5px 5px 10px white;
  font-size: 20px;
}

.email {
  grid-area: email;
}

.phone {
  grid-area: phone;
}

.resume {
  grid-area: resume;
}

.linkedin {
  grid-area: linkedin;
}

.about {
  grid-area: about;

  width: 700px;
  height: 370px;

  padding: 15px;
  margin-top: 25px;

  background-color: white;
  background-image: url(/portfolio/static/media/pink_puzzle.a346b46b.jpg);
  background-size: cover;
  background-position-y: -50px;

  border: 1.5px solid rgb(250, 120, 140);
  border-radius: 10px;
}


.about h4 {
  font-size: 22px;
  color: rgb(80, 80, 80);
  
  margin-top: 47px;
  margin-left: 150px;

  -webkit-transform: rotate(3.5deg);

          transform: rotate(3.5deg);
}

.bio-p {
  font-size: 15px;
  line-height: 1.5;

  background-color: rgb(208, 202, 206);

  border: 1px solid rgb(163, 149, 152);
  border-radius: 10px;

  margin-top: 120px;
  padding: 10px;
}

.projects {
  grid-area: projects;

  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;

  width: 700px;
  height: 383px;

  margin-top: 38px;
  padding: 10px;

  overflow-y: scroll;
  background-image: url(/portfolio/static/media/rose-close-up-pink-flower.5a57850d.jpg);
  background-color: white;
  background-position-y: -298px;
  background-size: cover;

  border: 1.5px solid rgb(208, 200, 204);
  border-radius: 10px;
}

.projects h4 {
  font-size: 22px;
  color: rgb(80, 80, 80);
  background-image: url("https://img.pngio.com/8-vintage-labels-vector-png-transparent-svg-onlygfxcom-vintage-labels-png-3000_1620.png");
}

.project-title {
  font-size: 17px;
}

.project-p {
  grid-area: text;

  font-size: 15px;
  line-height: 1.5;

  border-radius: 10px;

  padding: 10px;

  background-color: rgb(252, 156, 168);
}

/*sizing for WeTube screenshot*/
.weTube {
  height: 70px;
  width: 100px;

  transition: -webkit-transform .2s;

  transition: transform .2s;

  transition: transform .2s, -webkit-transform .2s;
}

/*effect scales the size up when you hover over the screenshot*/
.weTube:hover {
  -webkit-transform: scale(6);
          transform: scale(6);
}

/* beginning of styling for footer */
footer {
  grid-area: footer;

  display: flex;
  text-align: center;
  align-items: center;

  width: 700px;
  height: 200px;
  padding-left: 220px;
  padding-bottom: 130px;
}

#react {
  color: rgb(101, 219, 250);
}

#html {
  color: rgb(240, 102, 50);
}

#css {
  color: rgb(8, 120, 188);
}

#js {
  color: rgb(255, 203, 61)
}

#node {
  color: rgb(86, 158, 74)
}

#react,
#html,
#css,
#js,
#node {
  font-size: 60px;
  margin: 5px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
/* end of styling for footer *************************************/


/* start of styling for links ************************************/
a {
  text-decoration: none;
}

/* unvisited link */
a:link {
  color: rgb(70, 70, 70);
}

/* visited link */
a:visited {
  color: rgb(70, 70, 70);
}

/* mouse over link */
a:hover {
  color: rgb(139, 0, 97);
}

/* selected link */
a:active {
  color: rgb(70, 70, 70);
}
/* end of styling for links ***************************************/

.icons {
  position: relative;
  top: 3px;
}
